.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.header__right {
  display: flex;
  align-items: center;
}

.avatar{
  height: 35px!important;
  width: 35px!important;
}

.header__left {
  height: 15px;
  flex: 0.5;
  display: flex;
  background-color: white;
  padding: 10px;
  border-radius: 30px;
  color: gray;
  cursor: pointer;
  align-items: center;
}

.header__left > input {
  border: none;
  width: 100%;
  outline: none!important;
  cursor: pointer;
}

.header__right > h4 {
  margin-left: 10px;
  font-size: 13px;
}
