.app {
    display: flex;
    width: 100vw;
    height: 100vh;
}

.app>.body {
    padding: 30px;
    height: calc(90% - 30px)!important;
    overflow-y: scroll;
    flex: 0.8;
    color: white;
    background: linear-gradient(transparent, rgba(0, 0, 0, 1));
    background-color: rgb(8, 37, 60);
}
